.ratio-box {
  width: 100%;
  display: block;
  overflow: hidden;
  position: relative; }
  .ratio-box::before {
    content: ' ';
    padding-top: 100%;
    display: block; }
  .ratio-box ._image,
  .ratio-box ._content {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
  .ratio-box ._image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: var(--bg-src) !important;
    overflow: hidden; }
    .ratio-box ._image iframe {
      width: 100%;
      height: 100%; }
    .ratio-box ._image img, .ratio-box ._image video {
      width: 100%;
      height: auto; }
    .ratio-box ._image img {
      display: none; }

.ratio-box.-parent-height {
  height: 100%; }
  .ratio-box.-parent-height::before {
    content: none; }

.ratio-box.-square::before {
  padding-top: 100%; }

.ratio-box.-wide-screen::before {
  padding-top: 40%; }

.ratio-box.-half::before {
  padding-top: 50%; }

.ratio-box.-fullhd::before {
  padding-top: 56.25%; }

.ratio-box.-goldenratio::before {
  padding-top: 61.8%; }

.ratio-box.-three-quarters::before {
  padding-top: 75%; }

.ratio-box.-square-caption::before {
  padding-top: 87.5%; }

.ratio-box.-upright::before {
  padding-top: 118%; }

.ratio-box.-portrait::before {
  padding-top: 130%; }

@supports (object-fit: cover) {
  .ratio-box ._image:not(.-show-background) {
    background-image: none !important; }
    .ratio-box ._image:not(.-show-background) img, .ratio-box ._image:not(.-show-background) video {
      object-fit: cover;
      width: 100%;
      height: 100%;
      display: inline-block; } }

@supports (object-fit: cover) and (-ms-ime-align: auto) {
  .ratio-box ._image:not(.-show-background) video {
    height: auto; } }

